import React, { Component } from "react";
import { connect } from "react-redux";
import { compose } from "redux";

import RoleCreateForm from "./createForm";

import notification from "../../../../components/notification";
// import { Input, Select, Divider } from "antd";

import LayoutWrapper from "../../../../components/utility/layoutWrapper";
import PageHeader from "../../../../components/utility/pageHeader";
import IntlMessages from "../../../../components/utility/intlMessages";
import Button from "../../../../components/uielements/button";
import actions from "../../../../redux/employee/role/actions";
import { Box } from "../../../App/app.style";

const redirectTo = "/dashboard/setup/roles"


class CreateRole extends Component {


  componentDidMount = () => {
    const { createRole } = this.props;
    createRole()
  }
  handleSubmit = value => {
    const { storeRole } = this.props;
    storeRole(value);
  };

  componentDidUpdate() {
    // Display error notif
    if (this.props.role.error) {
      notification("error", this.props.role.error);
      this.props.clearError();
    }

    if (this.props.role.success) {
      this.props.history.replace(redirectTo)
    }
  }

  render() {
    // console.log(this.props);
    return (
      <LayoutWrapper>
        <Box>
          <PageHeader title={<IntlMessages id="roles.add.title" />}
            style={{ margin: 0 }}
          >
            <Button onClick={() => this.props.history.replace(redirectTo)}>
              <IntlMessages id="button.back" />
            </Button>
          </PageHeader>

          <RoleCreateForm
            auth={this.props.auth}
            submit={this.handleSubmit}
            role={this.props.role}
          />
        </Box>
      </LayoutWrapper>
    );
  }
}

const mapStateToProps = state => {
  return {
    auth: state.auth,
    role: state.role
  };
};

export default compose(
  connect(
    mapStateToProps,
    actions
  )
)(CreateRole);
